$buttonHoverColor: #C4C1C1
    
@mixin generalWidthHeight
    width: 100%
    height: 100vh

body
    @include generalWidthHeight
    font-family: 'Outfit', sans-serif

#root
    @include generalWidthHeight

.page
    @include generalWidthHeight

main
    @include generalWidthHeight
    display: flex
    flex-direction: column
    justify-content: center
    margin: auto

.drum-machine
    width: 500px
    height: 500px
    margin: auto

.drum-container
    width: 100%
    height: 400px
    display: grid
    grid-template-columns: 1fr 1fr 1fr
    grid-gap: 10px

.drum-pad
    display: flex
    align-items: center
    justify-content: center
    font-size: 2em
    border: 1px solid black
    border-radius: 10px
    cursor: pointer
    &:hover
        background-color: $buttonHoverColor 

.display
    width: 100%
    height: 90px
    margin-top: 10px
    border: 1px solid black
    border-radius: 10px
    display: flex
    align-items: center
    justify-content: center